<template>
  <div id="cl">
    <div class="c-main">

      <div class="m-header">
        <!-- 查询栏 -->
        <a-row>
          <a-col :span="5">
                <a-date-picker @change="onChangeStartTime" placeholder="开始时间"/>
          </a-col>
          <a-col :span="5">
            <a-date-picker @change="onChangeEndTime" placeholder="结束时间"/>
          </a-col>
          <a-col :span="5">
            <a-input style="width: 100%" v-model="corpNameInput" placeholder="公司名称"/>
          </a-col>
          <a-col :span="9">
              <a-radio-button @click="serach">查询</a-radio-button>
              <a-radio-button @click="reset">重置</a-radio-button>
              <a-radio-button @click="outCsv">导出CSV</a-radio-button>
          </a-col>
        </a-row>
      </div>

        <!-- 表格 -->
        <a-table
        :columns="columns"
        :pagination="false"
        :dataSource="data">
            <template slot="gmtCreate" slot-scope="gmtCreate">
              {{ new Date(gmtCreate).toLocaleString() }}
            </template>
            <template slot="creatorIsAdmin" slot-scope="creatorIsAdmin">
              {{ creatorIsAdmin ? '是' : '否' }}
            </template>
            <template slot="creatorIsBoss" slot-scope="creatorIsBoss">
              {{ creatorIsBoss ? '是' : '否' }}
            </template>

        </a-table>
        <br/>
        <a-pagination showSizeChanger
                      :current="pageCurrent"
                      :pageSize.sync="pageSize"
                      :total="pageTotal"
                      @change="changeThePage"
                      @showSizeChange="onShowSizeChange"/>
      </div>
  </div>
</template>

<script>
import { customerListFetch, exportCsv } from '@/services/custormer'

export default {
  data() {
    return {
      startDateInput: '',
      endDateInput: '',
      corpNameInput: '',
      columns: [
        {
          title: '公司名称',
          dataIndex: 'corpName'
        }, {
          title: '创建时间',
          dataIndex: 'gmtCreate',
          scopedSlots: { customRender: 'gmtCreate' },
        }, {
          title: '所在行业',
          dataIndex: 'industry'
        }, {
          title: '公司人数',
          dataIndex: 'corpCount'
        }, {
          title: '联系人userId',
          dataIndex: 'creatorUserId'
        }, {
          title: '创建者名称',
          dataIndex: 'creatorName'
        }, {
          title: '创建者是管理员',
          dataIndex: 'creatorIsAdmin',
          scopedSlots: { customRender: 'creatorIsAdmin' },
        }, {
          title: '创建者是老板',
          dataIndex: 'creatorIsBoss',
          scopedSlots: { customRender: 'creatorIsBoss' },
        }
      ],
      data: [],
      pageCurrent: 1,
      pageSize: 10,
      pageTotal: 0
    }
  },
  created() {
    this.getList();
  },
  mounted() {},
  methods: {
    // 获取列表
    getList() { 
      this.loading = true;

      let startDate = this.startDateInput;
      let endDate = this.endDateInput;
      let corpName = this.corpNameInput;

      let pageNumber = this.pageCurrent;
      let pageSize = this.pageSize;
 
      customerListFetch(startDate,endDate,corpName,pageNumber,pageSize).then((res) => {
        if (res.errcode === '0') {
          this.loading = false;
          // 分页数据
          this.pageCurrent = res.pageNumber;
          this.pageSize = res.pageSize;
          this.pageTotal = res.total;
          // 表单数据
          this.data = res.data;
        }
      });
    },
    serach() {
      this.getList();
    },
    reset() {
      this.startDateInput = '';
      this.endDateInput = '';
      this.corpNameInput = '';
      // 重置之后，查询一次
      this.getList();
    },
    onShowSizeChange(current, pageSize) {
      this.pageCurrent = 1;
      this.getList();
    },
    changeThePage(page, pageSize) {
      this.pageCurrent = page;
      this.pageSize = pageSize;
      this.getList();
    },
    onChangeStartTime(date, dateString) {
      // 毫秒值
      this.startDateInput = date._d.getTime();
    },
    onChangeEndTime(date, dateString) {
      // 毫秒值
      this.endDateInput = date._d.getTime();
    },
    outCsv() {
      // if (this.startDateInput == '' || this.endDateInput == '') {
      //   this.$message.warning('请选择起止日期！');
      //   return;
      // }
      // 请求导出接口
      // let startDate = this.startDateInput;
      // let endDate = this.endDateInput;

      let startDate = '2018-12-25';
      let endDate = '2018-12-29';

      exportCsv(startDate,endDate).then((res) => {
          console.log(res);
      });
    }
  },
  computed: {
    rowSelection() {
      return {}
    }
  },
}
</script>
<style lang='scss' scoped>
#cl {
  height: 100%;
  padding: 24px;
  padding-top: 88px;
  background-color: #f0f2f5;
  .c-main {
    background-color: #fff;
    padding: 23px 32px 17px;
    .m-header {
      overflow: hidden;
      margin-bottom: 24px;
    }
  }
}
button {
  outline: none;
  width: 65px;
  height: 32px;
  background: rgba(24, 144, 255, 1);
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  &:nth-child(3) {
    background-color: #fff;
    color: rgba(0, 0, 0, 0.65);
    border: 1px solid rgba(217, 217, 217, 1);
    margin-left: 8px;
  }
  &:nth-child(4) {
    float: right;
    margin-right: 32px;
  }
}
</style>
<style>
.ant-input-group {
  float: left;
}
.ant-input-group,
.ant-input,
.ant-select,
.ant-select-dropdown {
  font-size: 12px;
}

.ant-table-column-sortOrder {
  right: 44px;
}
.anticon-filter {
  right: 15px;
}
</style>
