/* eslint-disable indent */
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userinfo: {},
    helpLink: { link: '', name: '帮助' },
    footerRouterPath: [{
        path: '/privacy',
        name: '',
      },
      {
        path: '/clause',
        name: '',
      },
    ],
    companyInfo: [],
    loginInfo: {},
    registerInfo: {},
    verifyPhone: '',
  },
  mutations: {
    setusername: (state, userinfo) => {
      state.userinfo = userinfo
    },
    holdCompanyInfo: (state, companyInfo) => {
      state.companyInfo = companyInfo
    },
    holdLoginInfo: (state, loginInfo) => {
      state.loginInfo = loginInfo
    },
    holdRegisterInfo: (state, registerInfo) => {
      state.registerInfo = registerInfo
    },
    holdVerifyPhone: (state, verifyPhone) => {
      state.verifyPhone = verifyPhone
    },
  },
  actions: {

  },
})
