import request from '@/utils/request'


export function customerListFetch(startDate, endDate, corpName, pageNumber, pageSize) { // 客户管理列表
  return request.get(`${R_URL.customer.CUSTOMER_LIST}?startDate=${startDate}&endDate=${endDate}&corpName=${corpName}&pageNumber=${pageNumber}&pageSize=${pageSize}`, {
  })
    .then(res => res.data)
}

export function exportCsv(startDate, endDate) {
  return request.get(`${R_URL.customer.EXPORT_CSV}?startDate=${startDate}&endDate=${endDate}`, {
    headers: {
      // 'Content-Type': 'application/x-www-form-urlencoded',
      'Content-Type': 'application/csv',
      // 'Accept': 'multipart/form-data',
    }
  })
    .then(res => res.data)
}



